@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600&display=swap);
.title-name {
    font-family: Blacksword Regular,'Poppins', sans-serif;
    font-size: 4rem;
}

header {
    height: 100vh;
    padding-top: 7rem;
    
    
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ----------- CTA ------------ */

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;

}

/* ----------- HEADER SOCIALS ------------ */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 2rem;


}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ----------- PROFILE IMAGE ------------ */

.me {
    width: 22rem;
    height: 22rem;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-top: 4rem;
    overflow: hidden;
    
}

/* ----------- SCROLL DOWN ------------ */

.scroll__down {
    position: absolute;
    left: -1.5rem;
    bottom: -2rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .me {
        width: 50%;
    }
    
    header {
        height: 780px;
    }

    

}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    header {
        height: 780px;
    }

    .header__socials, .scroll__down {
        display: none;

    }
}
    

@font-face {
    font-family: 'Blacksword Regular',;
    font-style: normal;
    font-weight: normal;
    src: local('Blacksword Regular'), url(/bojan-tanasic-portfolio/static/media/Blacksword.743b56d0.woff) format('woff');
    }
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.8rem;
    font-size: 1.2rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.2);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}



#about {
    height: 50vh;
    margin-top: 20%;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;


}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns:repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 2px solid var(--color-primary);
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var
    (--color-primary-variant);
    cursor: default;

}

.about__icon {
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    #about {
        text-align: center;
        height: 60rem;

    }

    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    p {
        margin: 1rem 0 1.5rem;
    }

    h2 {
        padding-bottom: 5rem;
    }

    .about__me-image {
        margin-top: 30%;
        margin-bottom: 60%;
        
    }


}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    #about {
        text-align: center;
        height: 1300px;

    }
    
    h2 {
        padding-bottom: 5rem;
    }

    .about__me {
        width: 60%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 2px solid var(--color-primary);
    transition: var(--transition);
}

.experience__container > div:hover {
    background: transparent;
    cursor: default;
}

.experience__container >div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon {
    margin-top: 5px;
    color: var(--color-primary);
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    #experience > h2 {
        margin-bottom: 0;
        padding-bottom: 2rem;
    }

   .experience__container {
       grid-template-columns: 1fr;
   }

   .experience__container > div {
       width: 80%;
       padding: 2rem;
       margin: 0 auto;

   }

   .experience__content {
    padding: 1rem;
    
    }


}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    #experience > h2 {
        margin-bottom: 0;
        padding-bottom: 2rem;
    }

    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;

    }

    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }

}




.services__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    gap: 3rem;

}

.service {
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 0.2rem solid var(--color-primary);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head {
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list {
    padding: 3rem;
}

.service__list li {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list-icon {
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.icon1 {
    font-size: 3.01rem;
    color: var(--color-primary);
    margin-top: 1.4rem;
}

.icon2 {
    font-size: 1.7rem;
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.icon3 {
    font-size: 1.7rem;
    color: var(--color-primary);
    margin-top: 2rem;
}

.icon4 {
    font-size: 1.2rem;
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.service__list p {
    font-size: 0.9rem;
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .services__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;

    }

        .service {
            height: auto;


    }

}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {

    #services > h2 {
        margin-bottom: 0;
        padding-bottom: 2rem;
    }

    .services__container {
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    

}
.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;

}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 0.2rem solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/* ============== FORM ============== */

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }



}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
    }


}
footer {
    position:relative;
    bottom:0;
    z-index: 3;
    color: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    
}

footer a {
    color: var(--color-primary);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;


}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;

}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;

    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #221e18;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: rgb(255, 255, 255);
    --color-light: rgba(255, 255, 255, 0.6);
    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #221e18;
    background: var(--color-bg);
    color: rgb(255, 255, 255);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(/bojan-tanasic-portfolio/static/media/bg-texture-02.7c9950c4.png);
    
}
    

/* -------- GENERAL STYLES -------- */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, 
h2, 
h3, 
h4, 
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    margin-top: 80vh;
}

section > h2, 
section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #4db5ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a {
    color: #4db5ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition)
}

a:hover {
    color: rgb(255, 255, 255);
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.35rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: rgb(255, 255, 255);
    background: var(--color-white);
    color: #221e18;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #221e18;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 10rem;
    }
    
}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-top: 2rem;
    }
    
}


    


 


