#about {
    height: 50vh;
    margin-top: 20%;
}

.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;


}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns:repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 2px solid var(--color-primary);
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var
    (--color-primary-variant);
    cursor: default;

}

.about__icon {
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    #about {
        text-align: center;
        height: 60rem;

    }

    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    p {
        margin: 1rem 0 1.5rem;
    }

    h2 {
        padding-bottom: 5rem;
    }

    .about__me-image {
        margin-top: 30%;
        margin-bottom: 60%;
        
    }


}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    #about {
        text-align: center;
        height: 1300px;

    }
    
    h2 {
        padding-bottom: 5rem;
    }

    .about__me {
        width: 60%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

}