.title-name {
    font-family: Blacksword Regular,'Poppins', sans-serif;
    font-size: 4rem;
}

header {
    height: 100vh;
    padding-top: 7rem;
    
    
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ----------- CTA ------------ */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}

/* ----------- HEADER SOCIALS ------------ */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: 2rem;


}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ----------- PROFILE IMAGE ------------ */

.me {
    width: 22rem;
    height: 22rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    overflow: hidden;
    
}

/* ----------- SCROLL DOWN ------------ */

.scroll__down {
    position: absolute;
    left: -1.5rem;
    bottom: -2rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .me {
        width: 50%;
    }
    
    header {
        height: 780px;
    }

    

}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {
    header {
        height: 780px;
    }

    .header__socials, .scroll__down {
        display: none;

    }
}
    
