.services__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;

}

.service {
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 0.2rem solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service__head {
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list {
    padding: 3rem;
}

.service__list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list-icon {
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.icon1 {
    font-size: 3.01rem;
    color: var(--color-primary);
    margin-top: 1.4rem;
}

.icon2 {
    font-size: 1.7rem;
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.icon3 {
    font-size: 1.7rem;
    color: var(--color-primary);
    margin-top: 2rem;
}

.icon4 {
    font-size: 1.2rem;
    color: var(--color-primary);
    margin-top: 2.1rem;
}

.service__list p {
    font-size: 0.9rem;
}

/* ------ MEDIA QUERIES (MEDIUM DEVICES) ------- */
@media screen and (max-width: 1024px) {
    .services__container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;

    }

        .service {
            height: auto;


    }

}

/* ------ MEDIA QUERIES (SMALL DEVICES) ------- */
@media screen and (max-width: 600px) {

    #services > h2 {
        margin-bottom: 0;
        padding-bottom: 2rem;
    }

    .services__container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    

}